<template>
 <div ref="play" class="hello" id="play">{{msg}}</div>
</template>

<script>
import Player from "xgplayer";
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      xgplay: null
    };
  },
  mounted() {
    var _this = this;
    var play = null;
    // this.$nextTick(function() {
    //视频播放器
    play = new Player({
      id: "play",
      url: "https://v1.hyqfx.com/u/video_url/2018/1009/14558741.mp4",
      fluid: true,
      playbackRate: [0.5, 0.75, 1, 1.5, 2]
    });
    // });
    play.currentTime = 10
    play.on("timeupdate", function() {
      console.log();
      
    });
    console.log(_this.xgplay);
  },
  updated() {
    console.log(123);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



